import React, { useState } from "react";
import Modal from "react-modal";
import StarRating from "./StarRating";
import styled from "@emotion/styled";
import { postReview } from "../../api/details/details_api";

const CancelButton = styled.button`
  width: 200px;
  height: 46px;
  border-radius: 10px;
  border: 1px solid #2c39b5;
  color: #2c39b5;
  margin-top: 20px;
  background-color: #fff;
  font-size: 16px;
  font-weight: 400;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  &:hover {
    background: #2c39b5;
    color: #fff;
  }
`;

const SubmitButton = styled(CancelButton)``;

const ReviewForm = ({ isOpen, onRequestClose, ipayment }) => {
  const [rating, setRating] = useState(0);
  const [review, setReview] = useState("");

  const handleRate = async () => {
    if (rating === 0 || review.trim() === "") {
      //     ,   
      return;
    }
    try {
      const userInputData = {
        ipayment,
        contents: review,
        rating,
      };
      await postReview(userInputData); // API 
      //    
      setRating(0);
      setReview("");
      //   
      onRequestClose();
    } catch (error) {
      console.error("Error submitting review:", error);
      //   
    }
  };

  const handleClose = () => {
    //   
    onRequestClose();
    //  
    setRating(0);
    setReview("");
  };

  return (
    <Modal
      isOpen={isOpen}
      onRequestClose={onRequestClose}
      ipayment={ipayment}
      contentLabel="Review Form"
      style={{
        overlay: {
          backgroundColor: "rgba(0, 0, 0, 0.3)",
          zIndex: 1000, //   
        },
        content: {
          width: "65%", //    .
          maxWidth: "480px", //   .
          margin: "auto", //   .
          maxHeight: "40%", //     .

          overflowY: "auto", //       .
          top: "10%", //   10%   
          backgroundColor: "white", //     .
          border: "none", //  .
          borderRadius: "10px", //   .
          boxShadow: "0 4px 8px rgba(0, 0, 0, 0.1)", //  
          position: "fixed",
        },
      }}
    >
      <h2> </h2>
      <form
        onSubmit={e => {
          e.preventDefault(); //    
          handleRate();
        }}
      >
        <div
          style={{
            marginTop: "10px",
            marginBottom: "20px",
            marginLeft: "-90px",
          }}
        >
          <StarRating
            totalStars={5}
            rating={rating}
            onRate={setRating}
            isReviewing
          />
        </div>
        <div style={{ marginBottom: "10px" }}>
          <textarea
            value={review}
            onChange={event => setReview(event.target.value)}
            placeholder=" "
            rows={5}
            cols={50}
            style={{
              width: "100%", // Set width to 100%
              maxWidth: "450px", // Set maximum width
              minHeight: "150px",
              maxHeight: "250px",
              overflowY: "auto", // Add overflowY property
              backgroundColor: "#f5f5f5",
              textAlign: "left",
            }}
          />
        </div>
        <div style={{ display: "flex", justifyContent: "space-between" }}>
          <CancelButton type="button" onClick={handleClose}>
            
          </CancelButton>
          <SubmitButton type="submit"></SubmitButton>
        </div>
      </form>
    </Modal>
  );
};

export default ReviewForm;
